<template>
	<div class="myEcharts">
		<div :id="chartData.idData" :style="{width:chartData.style.width,height:chartData.style.height}"></div>
	</div>
</template>

<script>
	export default {
		props: ['chartData'],
		name: '', //组件名
		data() { //data数据
			return {

			}
		},
		methods: { //函数
			echartsSon() {
				let myChart = this.$echarts.init(document.getElementById(this.chartData.idData))
				myChart.setOption({
					title: {
						// text: '折线图堆叠',
						textStyle: {
							color: "rgba(0, 0, 0, 0.6)"
						}
					},
					tooltip: {
						trigger: 'axis'
					},
					legend: {
						data: this.chartData.options.legend.data,
						textStyle: {
							color: "rgba(0, 0, 0, 0.6)"
						}
					},
					grid: {
						left: '3%',
						right: '4%',
						bottom: '3%',
						containLabel: true
					},
					toolbox: {
						feature: {
							saveAsImage: {}
						}
					},
					xAxis: {
						type: 'category',
						boundaryGap: false,
						data: this.chartData.options.xAxis.data,
						axisLabel: { //修改坐标轴字体样式
							color: "rgba(0, 0, 0, 0.6)",
							fontSize: "12"
						},
					},
					yAxis: {
						type: 'value',
						axisLabel: { //修改坐标轴字体样式
							color: "rgba(0, 0, 0, 0.6)",
							fontSize: "12",
							lineStyle: {
								color: "rgba(0, 0, 0, 0.6)"
							}
						},
					},
					series: this.chartData.options.series
				});
			}
		},
		components: { //子组件

		},
		created() { //操作data数据
		console.log(this.chartData)
		},
		mounted() { //操作dom
			this.echartsSon()
		},
	}
</script>

