import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import File from '../components/file.vue'
import Echarts from '../components/echarts.vue'
import Table from '../components/table.vue'
import Three from '../components/three.vue'
import Carousel from '../components/Carousel.vue'
import setEchart from '../components/setEchart.vue'
import myEchart from '../components/myEcharts.vue'

Vue.use(VueRouter)

const routes = [
  {path: '/',component: Home,redirect:'/echarts',children:[
	{path: 'file',component: File},
	{path: 'echarts',component: Echarts},
	{path: 'table',component: Table},
	{path: 'three',component: Three},
	{path: 'carousel',component: Carousel},
	{path: 'setechart',component: setEchart},
	{path: 'myechart',component: myEchart}
	]
  },
]

const router = new VueRouter({
  routes
})

export default router
