<template>
	<div id="home">
		<el-container height='100%' direction="vertical">
			<el-header>Header</el-header>
			<el-container>
				<el-aside ref="aside" width="201px">
					<Menu ref='menu'></Menu>
				</el-aside>
				<el-container>
					<el-main>
						<router-view></router-view>
					</el-main>
					<el-footer>Footer</el-footer>
				</el-container>
			</el-container>
		</el-container>
	</div>
</template>

<script>
	import Menu from '../components/menu.vue'
	export default {
		data() {
			return {
				isCollapse: true,
				width: 0,
				tog: true,
				btn: false
			};
		},
		methods: {
			handleOpen(key, keyPath) {
				console.log(key, keyPath);
			},
			handleClose(key, keyPath) {
				console.log(key, keyPath);
			},
			clickbtn() {
				var btn = this.$refs.menu.$el.childNodes[0].childNodes[0].childNodes[0]
				btn.addEventListener('click', () => {
					if (this.$store.state.clitag == 0) {
						this.$store.state.clitag = 1
						this.width = this.$refs.menu.$el.childNodes[1].clientWidth
						this.btn = !this.btn
						setTimeout(() => {
							this.$store.state.clitag = 0
						}, 500)
					}
				})
			}
		},
		components: {
			Menu
		},
		mounted() {
			this.clickbtn()
		},
		watch: {
			width(newvalue, oldvalue) {
				var btn = this.$refs.menu.$el.childNodes[0],
					width = this.$refs.aside.$el.style.width,
					cliwidth = this.$refs.aside.$el.clientWidth;
				if (this.btn) {
					// console.log('收起')
					let timer = setInterval(() => {
						if (cliwidth > 65) {
							cliwidth -= 8
							this.$refs.aside.$el.style.width = cliwidth + 'px'
							btn.style.left = cliwidth + 19 + 'px'
						} else {
							clearInterval(timer)
						}
					}, 14)
				} else {
					// console.log('展开')
					let timer = setInterval(() => {
						if (cliwidth < 201) {
							cliwidth += 2
							this.$refs.aside.$el.style.width = cliwidth - 2 + 'px'
							btn.style.left = cliwidth + 19 + 'px'
						} else {
							clearInterval(timer)
						}
					}, 5)
				}
			}
		}
	}
</script>
<style scoped="scoped">
	*{
		margin: 0;
		padding: 0;
	}
	.el-header,
	.el-footer {
		background-color: #B3C0D1;
		color: #333;
		text-align: center;
		line-height: 60px;
	}
	.el-container,.is-vertical {
		height: 100%;
	}
	.el-aside {
		background-color: #D3DCE6;
		color: #333;
		text-align: center;
		line-height: 200px;
		overflow-x: hidden;
	}

	.el-main {
		background-color: #E9EEF3;
		color: #333;
		text-align: center;
		/* line-height: 160px; */
		line-height: 20px !important;
		max-height: 641.6px;
	}

	body>.el-container {
		margin-bottom: 40px;
	}

	.el-container:nth-child(5) .el-aside,
	.el-container:nth-child(6) .el-aside {
		line-height: 260px;
	}

	.el-container:nth-child(7) .el-aside {
		line-height: 320px;
	}

	.el-menu-vertical-demo:not(.el-menu--collapse) {
		width: 200px;
		min-height: 400px;
	}
</style>
