<template>
	<div id="table">
		<el-table @select="grouptogg" @select-all='toggele' ref='multipleTable' :data="tableData" style="width: 100%;margin-bottom: 20px;"
		 row-key="id" border @selection-change="handleSelectionChange" default-expand-all :tree-props="{children: 'children', hasChildren: 'hasChildren'}">
			<el-table-column align='center' type="selection" width="55">
			</el-table-column>
			<el-table-column align='center' prop="date" label="日期" sortable width="180">
			</el-table-column>
			<el-table-column align='center' prop="name" label="姓名" sortable width="180">
			</el-table-column>
			<el-table-column align='center' prop="address" label="地址">
			</el-table-column>
		</el-table>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				checkedKeys: false,
				tableData: [{
					id: 1,
					date: '2016-05-02',
					name: '王小虎',
					address: '上海市普陀区金沙江路 1518 弄'
				}, {
					id: 2,
					date: '2016-05-04',
					name: '王小虎',
					address: '上海市普陀区金沙江路 1517 弄'
				}, {
					id: 3,
					date: '2016-05-01',
					name: '王小虎',
					address: '上海市普陀区金沙江路 1519 弄',
					children: [{
						id: 31,
						date: '2016-05-01',
						name: '王小虎',
						address: '上海市普陀区金沙江路 1519 弄',
					}, {
						id: 32,
						date: '2016-05-01',
						name: '王小虎',
						address: '上海市普陀区金沙江路 1519 弄'
					}]
				}, {
					id: 4,
					date: '2016-05-03',
					name: '王小虎',
					address: '上海市普陀区金沙江路 1516 弄'
				}],
				multipleSelection: [],
			}
		},
		methods: {
			handleSelectionChange(val) {
				this.multipleSelection = val;
			},
			toggele(selection) {
				this.checkedKeys = !this.checkedKeys;
				this.splite(this.tableData, this.checkedKeys);
			},
			splite(data, flag) {
				data.forEach(row => {
					this.$refs.multipleTable.toggleRowSelection(row, flag);
					if (row.children) {
						this.splite(row.children, this.checkedKeys);
					}
				});
			},
			grouptogg(select, row) {
				let checkrow = [];
				checkrow.push(row);
				if (row.isCheck == true) {
					this.clearRow(checkrow);
				} else {
					this.checkRow(checkrow);
				}
			},
			clearRow(data) {
				Array.from(data).forEach(row => {
					row.isCheck = false; //给这行数据设置一个选中字段为false
					this.$refs.multipleTable.toggleRowSelection(row, false);
					if (row.children) this.clearRow(row.children); //有子集就递归  没子集了就不循环了 
				})
			},
			checkRow(data) {
				Array.from(data).forEach(row => {
					row.isCheck = true; //选中是字段值为true
					this.$refs.multipleTable.toggleRowSelection(row, true);
					if (row.children) this.checkRow(row.children);
				})
			},
		},
		mounted() {

		}
	}
</script>

<style scoped="scoped">
	#table {
		margin-top: 50px;
	}

	.el-table__header-wrapper {
		height: 20px;
	}
</style>
