<template>
	<div id="chart">
		<div id="myChart1" :style="{width: '300px', height: '300px',margin:'20px 0'}"></div>
		<div id="myChart2" :style="{width: '500px', height: '300px',margin:'20px 0'}"></div>
		<div id="myChart3" :style="{width: '500px', height: '300px',margin:'20px 0'}"></div>
		<div id="myChart4" :style="{width: '500px', height: '300px',margin:'20px 0'}"></div>
	</div>
</template>

<script>
	import * as echarts from 'echarts'
	// import {bar,tooltip,title} from 'echarts'
	// var echarts = require('echarts');
	// require("echarts/lib/chart/bar");
	// 引入提示框和title组件
	// require("echarts/lib/component/tooltip");
	// require("echarts/lib/component/title");
	export default {
		name: "Echarts",
		data() {
			return {
				myChart: null,
			}
		},
		mounted() {
			this.showChart()
			// setInterval(()=>{
			// 	this.radius=['25%','50%']
			// },1000)
			// this.taowa()
		},
		beforeDestroy() { //销毁实例
			if (!this.myChart) { //解决报错：cannot read property 'dispose' of null
				return;
			}
			this.myChart.dispose()
			this.myChart = null
		},
		methods: {
			showChart(radius=['20%', '45%']) {
				this.myChart = echarts.init(document.getElementById("myChart1")); //初始化echarts实例，id名称对应template中的id名称
				let myChart2 = echarts.init(document.getElementById('myChart2'))
				let myChart3 = echarts.init(document.getElementById('myChart3'))
				let myChart4 = echarts.init(document.getElementById('myChart4'))
				let option1 = {
					tooltip: { //弹框
						trigger: 'item',
						position: 'inside', //防止弹框跑出div，内容显示不全
						formatter: "{b}: {c}<br/>({d}%)" //b为data中的name值 c为value值 d为选中区域占比值
					},
					series: [{ //内部环形图
							color: ['#3196fa', '#ef4864', '#facc14'],
							type: 'pie',
							radius: radius,
							label: {
								normal: {
									position: 'inner',
									textStyle: {
										fontSize: '12',
									},
									formatter: '{b}\n{d}%',
									position: 'inside',
								},
							},
							labelLine: {
								normal: {
									show: false,
								}
							},
							data: [ //内部环形图数据
								{
									value: '150',
									name: '大一'
								},
								{
									value: '170',
									name: '大二'
								},
								{
									value: '190',
									name: '大三'
								}
							],
						},
						{ //外面的环形图
							type: 'pie',
							radius: ['50%', '60%'],
							label: {
								normal: {
									show: false,
								}
							},
							data: [ //外部环形图数据
								{
									value: '100',
									name: '男',
									itemStyle: {
										normal: {
											color: '#65b2ff'
										}
									}
								},
								{
									value: '50',
									name: '女',
									itemStyle: {
										normal: {
											color: '#c0d8f1'
										}
									}
								},
								{
									value: '110',
									name: '男',
									itemStyle: {
										normal: {
											color: '#ed7085'
										}
									}
								},
								{
									value: '60',
									name: '女',
									itemStyle: {
										normal: {
											color: '#f59cab'
										}
									}
								},
								{
									value: '120',
									name: '男',
									itemStyle: {
										normal: {
											color: '#f8d751'
										}
									}
								},
								{
									value: '70',
									name: '女',
									itemStyle: {
										normal: {
											color: '#f9e596'
										}
									}
								}
							]
						}
					]
				};
				this.myChart.setOption(option1);
				let option2 = {
					color: ['#2f89cf'], //柱子颜色
					tooltip: { //鼠标悬停
						trigger: 'axis',
						axisPointer: { // 坐标轴指示器，坐标轴触发有效
							type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
						}
					},
					grid: { //修改图表大小
						left: '0%',
						right: '0%',
						top: "10px",
						bottom: '4%',
						containLabel: true
					},
					xAxis: [{
						type: 'category',
						data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
						axisTick: {
							alignWithLabel: true
						},
						axisLabel: { //修改坐标轴字体样式
							color: "rgba(0, 0, 0, 1.0)",
							fontSize: "12"
						},
						axisLine: { //x轴样式
							show: false
						}
					}],
					yAxis: [{
						type: 'value',
						axisLabel: { //修改坐标轴字体样式
							color: "rgba(0, 0, 0, 1.0)",
							fontSize: "12"
						},
						axisLine: {
							lineStyle: { //坐标轴线条样式
								color: "rgba(0, 0, 0, 1.0)",
							}

						},
						splitLine: {
							lineStyle: { //分割线样式
								color: "rgba(0, 0, 0, 1)"
							}
						}
					}],
					series: [{
						name: '直接访问',
						type: 'bar',
						barWidth: '35%',
						data: [10, 52, 200, 334, 390, 330, 220],
						itemStyle: { //修改柱子圆角
							barBorderRadius: 5
						}
					}]
				};
				myChart2.setOption(option2);
				let option3 = {
					title: {
						// text: '折线图堆叠',
						textStyle: {
							color: "rgba(0, 0, 0, 0.6)"
						}
					},
					tooltip: {
						trigger: 'axis'
					},
					legend: {
						data: ['邮件营销', '联盟广告', '视频广告', '直接访问', '搜索引擎'],
						textStyle: {
							color: "rgba(0, 0, 0, 0.6)"
						}
					},
					grid: {
						left: '3%',
						right: '4%',
						bottom: '3%',
						containLabel: true
					},
					toolbox: {
						feature: {
							saveAsImage: {}
						}
					},
					xAxis: {
						type: 'category',
						boundaryGap: false,
						data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
						axisLabel: { //修改坐标轴字体样式
							color: "rgba(0, 0, 0, 0.6)",
							fontSize: "12"
						},
					},
					yAxis: {
						type: 'value',
						axisLabel: { //修改坐标轴字体样式
							color: "rgba(0, 0, 0, 0.6)",
							fontSize: "12",
							lineStyle: {
								color: "rgba(0, 0, 0, 0.6)"
							}
						},
					},
					series: [{
							name: '邮件营销',
							type: 'line',
							stack: '总量',
							data: [120, 132, 101, 134, 90, 230, 210]
						},
						{
							name: '联盟广告',
							type: 'line',
							stack: '总量',
							data: [220, 182, 191, 234, 290, 330, 310]
						},
						{
							name: '视频广告',
							type: 'line',
							stack: '总量',
							data: [150, 232, 201, 154, 190, 330, 410]
						},
						{
							name: '直接访问',
							type: 'line',
							stack: '总量',
							data: [320, 332, 301, 334, 390, 330, 320]
						},
						{
							name: '搜索引擎',
							type: 'line',
							stack: '总量',
							data: [820, 932, 901, 934, 1290, 1330, 1320]
						}
					]
				};
				myChart3.setOption(option3);
				let option4 = {
					title: {
						// text: '某站点用户访问来源',
						// subtext: '纯属虚构',
						// left: 'center'
					},
					tooltip: {
						trigger: 'item',
						formatter: '{a} <br/>{b} : {c} ({d}%)'
					},
					legend: {
						// orient: 'vertical',
						left: 'left',
						data: ['直接访问', '邮件营销', '联盟广告', '视频广告', '搜索引擎'],
						textStyle: {
							color: '#000000'
						}
					},
					series: [{
						name: '访问来源',
						type: 'pie',
						radius: '55%',
						center: ['50%', '60%'],
						data: [{
								value: 335,
								name: '直接访问'
							},
							{
								value: 310,
								name: '邮件营销'
							},
							{
								value: 234,
								name: '联盟广告'
							},
							{
								value: 135,
								name: '视频广告'
							},
							{
								value: 1548,
								name: '搜索引擎'
							}
						],
						emphasis: {
							itemStyle: {
								shadowBlur: 10,
								shadowOffsetX: 0,
								shadowColor: 'rgba(0, 0, 0, 0.5)'
							}
						}
					}]
				}
				myChart4.setOption(option4);
			},
			// taowa(){
			// 	var radius=['20%', '45%']
			// 	setInterval(()=>{
			// 		radius=['40%','60%']
			// 		this.showChart(radius)
			// 	},1000,()=>{
			// 		radius=['20%', '45%']
			// 		this.showChart(radius)
			// 	})
				
			// }
		}
	}
</script>

<style>
	*{
		margin: 0;
		padding: 0;
	}
	#chart {
		display: flex;
		flex-wrap: wrap;
		height: 580px;
		overflow: auto;
	}
</style>
