			<!-- <el-radio-button :label="true"></el-radio-button> -->
		</el-radio-group>

		<el-menu ref="menu" router default-active="1-4-1" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose"
		 :collapse="isCollapse">
			<el-submenu index="1">
				<template slot="title">
					<i class="el-icon-user"></i>
					<span slot="title">导航一</span>
				</template>
				<el-menu-item-group>
					<span slot="title">分组一</span>
					<el-menu-item index="/echarts">数据可视化</el-menu-item>
					<el-menu-item index="/file">上传图片</el-menu-item>
				</el-menu-item-group>
				<el-menu-item-group title="分组2">
					<el-menu-item index="/table">数据表格</el-menu-item>
				</el-menu-item-group>
				<el-submenu index="1-4">
					<span slot="title">3D</span>
					<el-menu-item index="/three">正方体</el-menu-item>
				</el-submenu>
			</el-submenu>
			<el-menu-item index="/carousel">
				<i class="el-icon-menu"></i>
				<span slot="title">轮播</span>
			</el-menu-item>
			<el-menu-item index="3" disabled>
				<i class="el-icon-document"></i>
				<span slot="title">导航三</span>
			</el-menu-item>
			<el-menu-item index="/setechart">
				<i class="el-icon-setting"></i>
				<span slot="title">导航四</span>
			</el-menu-item>
		</el-menu>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				isCollapse: false,
				tog: false,
				width: 200,
			};
		},
		methods: {
			handleOpen(key, keyPath) {
				console.log(key, keyPath);
			},
			handleClose(key, keyPath) {
				console.log(key, keyPath);
			},
			open(el) {
				if (el.target.tagName === 'INPUT') return
				if (this.$store.state.clitag == 0) {
					this.tog = !this.tog
					this.width = this.$refs.menu.$el.clientWidth
				}
			}
		},
		mounted() {},
		watch: {}
	}
</script>

<style scoped="scoped">
	.el-menu-vertical-demo:not(.el-menu--collapse) {
		width: 200px;
		min-height: 400px;
	}

	.el-radio-group {
		z-index: 50;
		position: absolute;
		left: 220px;
		top: 80px;
		opacity: 0.5;
	}
</style>
